.badge-primary,
.btn-group {
  min-width: 40px;
  min-height: 40px;
}

#header {
  background-color: black;
  color: white;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  text-align: center;
  font-weight: bold;
}

.footer {
  background-color: #f5f5f5;
  color: black;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  text-align: center;
  padding: 20px;
}

.main {
  flex: 1;
}

#expanded {
  font-size: 90px;
}


#border-separate:checked ~ table {
  border-collapse: separate;
}

table {
  border-collapse: collapse;
}
.word-search  td {
  padding: unset;
  text-align: center;
}
.word-search  tr {
  border: unset;
}
.btn-disabled {
  pointer-events: none;
  display: inline-block;
}
