@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
.jumbotron {
  margin-bottom: 8px !important;
  padding-top: 20px !important;
  padding-bottom: 20px !important;
}
.selected {
  background-color: yellow;
}
div {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.badge-primary,
.btn-group {
  min-width: 40px;
  min-height: 40px;
}

#header {
  background-color: black;
  color: white;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  text-align: center;
  font-weight: bold;
}

.footer {
  background-color: #f5f5f5;
  color: black;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  text-align: center;
  padding: 20px;
}

.main {
  flex: 1 1;
}

#expanded {
  font-size: 90px;
}


#border-separate:checked ~ table {
  border-collapse: separate;
}

table {
  border-collapse: collapse;
}
.word-search  td {
  padding: unset;
  text-align: center;
}
.word-search  tr {
  border: unset;
}
.btn-disabled {
  pointer-events: none;
  display: inline-block;
}

.found1 {
  background-color: gold !important;
  border-color: gold !important;
}
.found2 {
  background-color: cadetblue !important;
  border-color: cadetblue !important;
}
.found3 {
  background-color: green !important;
  border-color: green !important;
}
.found4 {
  background-color: beige !important;
  border-color: beige !important;
}
.found5 {
  background-color: rgb(231, 34, 34) !important;
  border-color: rgb(231, 34, 34) !important;
}
.found6 {
  background-color: orange !important;
  border-color: orange !important;
}
.found7 {
  background-color: darkgreen !important;
  border-color: darkgreen !important;
}
.found8 {
  background-color: pink !important;
  border-color: pink !important;
}
.found9 {
  background-color: brown !important;
  border-color: brown !important;
}
.found10 {
  background-color: slateblue !important;
  border-color: slateblue !important;
}
.found11 {
  background-color: grey !important;
  border-color: grey !important;
}


.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10vh;
}

.App-header {
  flex: .2 1;
  text-align: center;
  background-color: #222;
  padding: 8px;
  color: #fff;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.App-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: .1 1;
  font-size: 0.9em;
  font-style: italic;
  background-color: #000;
  color: #fff;
  text-align: center;
}

.App-footer > h5 {
  margin-left: 8px;
}

.AttemptsLeft {
  font-style: italic;
  font-size: 0.9em;
}

.AttemptsLeft-Number {
  color: #ff6600;
}

.Word > span {
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 3em;
  font-weight: 600;
}

.Word > span:last-child {
  margin-right: 0;
}

.App-Restart > button {
  border-radius: 8px;
  padding: 6px 12px;
  background: linear-gradient(to bottom, #ff6600 0%, #ff6600 100%);
  font-weight: 300;
  font-size: .6em;
  border: none;
  color: #fff;
  cursor: pointer;
}

.App-Restart > button:focus {
  outline: none;
}

.App-Restart > button:active {
  box-shadow: inset 0px 1px 3px 2px rgba(0, 0, 0, 0.4);
	cursor: pointer;
}

.App-Restart > button:hover {
  text-decoration: none;
  color: #fff;
  background: linear-gradient(to bottom, #000 0%, #000 100%);
}

.LettersRow {
  display: flex;
}

.LettersRow > .LetterBlock {
  margin-right: 1em;
}

.LettersRow > .LetterBlock:last-child {
  margin-right: 0;
}

.LetterBlock {
  display: flex;
  justify-content: center;
  cursor: pointer;
  border: 1px solid #999;
  border-radius: 5px;
  padding: 5px;
  min-width: 1.2em;
  color: #000;
}

.LetterBlock:hover {
  text-decoration: none;
  color: #fff;
  background: linear-gradient(to bottom, #000 0%, #000 100%);
}

.LetterBlock > span {
  font-weight: 400;
}

.VirtualKeyboard {
  text-transform: uppercase;
}

.VirtualKeyboard > div {
  margin-bottom: 8px;
}

.VirtualKeyboard > div:last-child {
  margin-bottom: 0;
}

.Game-content {
  margin-left: auto;
  margin-right: auto;
}

.Game-InputPanel {
  flex: .5 1 50%;
  display: flex;
  margin-left: 20%;
  flex-direction: column;
  padding-top: 8px;
}

.Game-InputPanel .Game-Word {
  flex: .1 1;
}

.Game-InputPanel .Game-AttemptsLeft {
  flex: .1 1;
}

.Game-InputPanel .Game-VirtualKeyboard {
  flex: .8 1;
}

.Game-InputPanel .Game-GameWin {
  flex: .8 1;
  font-weight: 600;
  font-size: 1.3em;
}

.Game-InputPanel .Game-GameOver {
  flex: .8 1;
  font-weight: 600;
  font-size: 1.3em;
}

.Game-Hangman {
  flex: .5 1 50%;
  margin-top: -80px;
}

.Game-SideBySide {
  display: flex;
  flex-wrap: wrap;
}

.App {
  display: flex;
  flex-direction: column;
}

.App a {
  text-decoration: none;
  color: #ff6600;
}

.App a:hover {
  text-decoration: none;
}

.topContainer{
    margin: 10%;
    display: flex; 
    justify-content: space-around;

}


